import React, { Component, Suspense } from 'react';
import { Collapse } from 'react-bootstrap';
const EmbedCode = React.lazy(() => import('../../../PublishCommonComponents/EmbedCode'));
const NeedSupport = React.lazy(() => import('../../../CommonComponents/NeedSupport'));
class WordPressEmbed extends Component {
    constructor(props) {
        super(props);
    }
    state = { genrateCode: false };

    render() {
        const { genrateCode } = this.state
        return  <div>
                    <div className="block-center my-3">
                        <button type="button" className="btn btn-primary mb-2" onClick={() => this.setState({ genrateCode: !genrateCode })}>
                            <span className="svg-icon">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                    <path fill="none" d="M0 0h24v24H0z" />
                                    <path d="M24 12l-5.657 5.657-1.414-1.414L21.172 12l-4.243-4.243 1.414-1.414L24 12zM2.828 12l4.243 4.243-1.414 1.414L0 12l5.657-5.657L7.07 7.757 2.828 12zm6.96 9H7.66l6.552-18h2.128L9.788 21z" fill="currentColor" />
                                </svg>
                            </span>
                            Genrated Embed Code
                        </button>
                    </div>
                    <Collapse in={genrateCode}>
                        <div className="m-auto">
                        <div className="embed_code">
                            <h5 className="mb-5 fw-boldest">Embed Widget On WordPress Website</h5>
                            <h6>1. Adjust the width and height of the widget as per your requirement</h6>
                            <Suspense fallback={''}>
                                <EmbedCode copyText={'2. Copy the generated embed code for your Wordpress Website'} />
                            </Suspense>
                            <h6 className="mb-5">3. Paste the embed code into the backend of the page</h6>
                            <ul className="list-unstyled list-points fs-6">
                                <li className="mb-3">Log in to your WordPress</li>
                                <li className="mb-3">Select and edit the page where you want to embed the widget and apply changes to display it on your website</li>
                                <li className="mb-3">Click on (+) to add new block and choose custom HTML</li>
                                <li>Paste the copied code</li>
                                <li>Click on Publish/Update post and You're Done!</li>
                            </ul>
                        </div>
                        <div className="pt-2">
                            <Suspense fallback={''}>
                                <NeedSupport content={'Need Help?'} supportUrl={'https://player.vimeo.com'} videoUrl={'https://player.vimeo.com/video/510156339'} />
                            </Suspense>
                        </div>
                        </div>
                    </Collapse>
                </div>
    }
}

export default WordPressEmbed;